.login{
    width: 100vw;
    height: 100vh;
    background:linear-gradient(
            to bottom,
            rgba(0,0,0,0) 0%,
            rgba(0,0,0,1) 100%
        ),
        url("https://user-images.githubusercontent.com/33485020/108069438-5ee79d80-7089-11eb-8264-08fdda7e0d11.jpg");
        background-size: cover;
        position: relative;

    .top{
        .wrapper{
            padding: 20px 50px;
            display: flex;
            align-items: center;
            justify-content: space-between;

            .logo{
                height: 40px;
            }
        }
    }

    .container{
        width: 100%;
        height: 100%;
        position: absolute;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: white;
        top: 0;
        left: 0;

        form{
            width: 300px;
            height: 300px;
            padding: 30px;
            border-radius: 5px;
            background-color: var(--main-color);
            display: flex;
            flex-direction: column;
            justify-content: space-around;

            input{
                height: 40px;
                border-radius: 5px;
                background-color: grey;
                color: white;
                padding-left: 10px;

                &::placeholder{
                    color: lightgray;
                }
            }

            button{
                height: 40px;
                border-radius: 5px;
                background-color: red;
                color: white;
                border: none;
                font-size: 18px;
                font-weight: 500;
                cursor: pointer;
            }

            span{
                color: lightgray;
                b{
                    color: white;
                }
            }
        }
    }
}